import React from "react";
import { Link } from "gatsby";

import {AcademicCapIcon} from '@heroicons/react/solid'

const BlogPage = ({ pageContext: { articlesList } }) => {
  console.log("### BlogPage articlesList:", articlesList);

  return (

    <div className="container mx-auto pt-20">
      
      <AcademicCapIcon className="h-20 w-20 text-white"/>
      
      <h1 className="font-mono text-white text-xl">BlogPage</h1>
      <hr />
      <Link to="/" className="font-serif text-white text-xl">back to black</Link>
      <br />
      <hr />
      <br />
      <br />
      <br />

      <ul>
        {articlesList.map((article) => {
          return (
            <li className="font-mono text-white text-xl" key={article.id}>
              <Link to={`/blog/post/${article.slug}`}>{article.title}</Link>
            </li>
          );
        })}
      </ul>



      
    </div>
  );
};

export default BlogPage;
